

html {
    --red-dark: #e66767;
    --red-light: #dffaff;
    --blue-dark: #546de5;
    --blue-light: #778beb;
    --border: rgba(0, 0, 0, .2);
    --background: white;
    --text: black;
}

body {
    background-color: Black;
}

h1 {
    text-align: center;
    color: var(--blue-light);
    text-transform: uppercase;
    background-color: var(--red-light);
    border: 2px solid var(--blue-light);
    padding: 1rem;
}


a {
    display: block;
    color: var(--blue-dark);
    text-align: center;
    text-decoration: none;
    padding: 2rem;

    :hover {
        color: var(--red-dark);
    }
}


.container {
    background-color: aliceblue;
    margin-left: 25%;
    margin-right: 25%;
}

main {
    height: 40rem;
}

.breath {
    background-color: var(--red-dark);
    margin-left: auto;
    margin-right: auto;
    width: 7rem;
    height: 7rem;
    border-radius: 20%;
    animation-name: respire;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}

@keyframes respire {
    0% {height: 1rem;}
    50% {height: 35rem;}
    100% {height: 1rem;}
}